}

#chk, #chk2, #chk3, #chk4 {
    position: absolute;
    visibility: hidden;
}

@media screen and (max-width: 550px) {
    .close-btn {
        display: block;
        position: absolute;
        top: 40px;
        right: 40px;
    }

    .open-menu-btn{
        display: block;
    }

    .navbar-ul {
        position: fixed;
        width: 100%;
        height: 100vh;
        left: 100vh;
        right: 0;
        top: 0;
        padding: 80px;
        line-height: normal;
        -webkit-transition: 0.7s;
        transition: 0.7s;
    }
    .navbar-li {
      padding: 10px;  
      float: none;
      display: block;
    }

    #chk:checked ~ .navbar-ul {
        left: 0vh;
    }
    #chk2:checked ~ .navbar-ul {
        left: 100vh;
    }
    #chk3:checked ~ .navbar-ul {
        left: 100vh;
    }
    #chk4:checked ~ .navbar-ul {
        left: 100vh;
    }
    

  }

